.social-icon, .arrow-icon {
    width: 35px;
    height: 35px;
}

#kacper-img, #mask-img {
    position: absolute;
    width: 100%;
    background-repeat: no-repeat;
    background-size:  auto 60%;
    background-position: -50px bottom;
    background-attachment: fixed;
}

#kacper-img {
    height: 100%;
    z-index: 0;
}

#mask-img {
    height: 100vh;
    z-index: 10;
    bottom: 0;
}

.bg-texture-1 {
    background-image: url('./assets/textures/double-bubble-outline.png');
}

.bg-texture-2 {
    background-image: url('./assets/textures/so-white.png');
}

.bg-texture-3 {
    background-image: url('./assets/textures/spikes.png');
}

.bg-texture-4 {
    background-image: url('./assets/textures/triangle-mosaic.png')
}

.bg-texture-5 {
    background-image: url('./assets/textures/y-so-serious-white-50op.png')
}

.bg-texture {
    background-repeat: repeat;
}

@screen sm {
    #kacper-img, #mask-img {
        background-size:  auto 80%;
        background-position: left bottom;
    }
}

@screen 2xl {
    #kacper-img, #mask-img {
        background-position:  calc((100vw - 1536px)/2) bottom;
    }
}

.break-word {
    word-break: break-word;
}
