.social-icon, .arrow-icon {
  width: 35px;
  height: 35px;
}

#kacper-img, #mask-img {
  position: absolute;
  width: 100%;
  background-repeat: no-repeat;
  background-size:  auto 60%;
  background-position: -50px bottom;
  background-attachment: fixed;
}

#kacper-img {
  height: 100%;
  z-index: 0;
}

#mask-img {
  height: 100vh;
  z-index: 10;
  bottom: 0;
}

.bg-texture-1 {
  background-image: url(/static/media/double-bubble-outline.0694bcab.png);
}

.bg-texture-2 {
  background-image: url(/static/media/so-white.c00e4435.png);
}

.bg-texture-3 {
  background-image: url(/static/media/spikes.ab304b3d.png);
}

.bg-texture-4 {
  background-image: url(data:image/png;base64,)
}

.bg-texture-5 {
  background-image: url(data:image/png;base64,)
}

.bg-texture {
  background-repeat: repeat;
}

@media (min-width: 640px) {
  #kacper-img, #mask-img {
    background-size:  auto 80%;
    background-position: left bottom;
  }
}

@media (min-width: 1536px) {
  #kacper-img, #mask-img {
    background-position:  calc((100vw - 1536px)/2) bottom;
  }
}

.break-word {
  word-break: break-word;
}

